
#swiper {
    width: 1200px;
    height: 401px;
    margin: 0 auto;
    /* background-color: #000; */
}
.swiper-container-horizontal {
    width: 100%;
    height: 100%;
}
.swiper-container.swiper-container-horizontal img {
    width: 100%;
    height: 401px;
}
@media screen and (max-width:1200px) {
#swiper {
        width: 100%;
        height: 401px;
}
.swiper-container.swiper-container-horizontal img {
        width: 100%;
        height: 401px;
}
}
@media screen and (max-width: 767px) {
#swiper {
        width: 100%;
        height: 1.5rem;
}
.swiper-container.swiper-container-horizontal img {
        width: 100%;
        height: 100%;
}
}



